.container__main .container__main--title {
  font-size: 2.5rem;
  color: #e74c3c; }

.container__main p {
  text-align: justify;
  font-size: 17px;
  line-height: 180%;
  word-wrap: break-word; }

.container__main ul li {
  font-size: 16px;
  list-style-type: circle;
  line-height: 180%; }

.container__main pre {
  font-size: 16px; }
  .container__main pre code {
    line-height: 150%; }

.container__main img {
  width: auto;
  max-width: 100%;
  height: auto;
  margin: 0 auto;
  display: block; }

.container__main table {
  border-collapse: collapse;
  width: 100%; }

.container__main table td, .container__main table th {
  border: 1px solid #ddd;
  padding: 8px; }

.container__main table tr:nth-child(even) {
  background-color: #f2f2f2; }

.container__main table tr:hover {
  background-color: #ddd; }

.container__main table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #4CAF50;
  color: white; }
