.Header__container {
  position: fixed;
  background: #f5f6fa;
  width: 280px;
  height: 100%;
  padding: 50px 10px;
  text-align: right;
  top: 0; }
  .Header__container a {
    text-decoration: none; }
    .Header__container a h1 {
      color: #354052;
      line-height: 150%;
      cursor: pointer;
      font-weight: bold; }
      .Header__container a h1:hover {
        text-decoration: underline; }
  .Header__container .Header__container--subName {
    font-family: 'Space Mono';
    font-style: italic; }
  .Header__container .Header__container--note {
    margin-top: 10px;
    font-family: 'Space Mono';
    line-height: 180%;
    color: #6e6e6e; }

@media only screen and (max-width: 480px) {
  .Header__container {
    width: calc(100% - 20px);
    max-width: 100%;
    position: unset; } }
